.theme-switch__icon {
  fill: #fff;
  height: 12px;
  width: 12px;
  margin-left: 4px;
  margin-right: 4px;
}

.theme-switch__control {
  width: 32px;
  height: 16px;
  border-radius: 50em;
  flex-shrink: 0;
  align-items: center;
  padding: 2px 0;
  display: flex;
  position: relative;
}

.theme-switch__toggle {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__track {
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  background-color: #333;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__marker {
  width: 13px;
  height: 13px;
  z-index: 2;
  pointer-events: none;
  background-color: #fff;
  border-radius: 50%;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  transform: translateX(3px);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .25);
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: #d3d3d4;
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  transform: translateX(18px);
}

.theme-switch__toggle:focus + .theme-switch__track, .theme-switch__toggle:active + .theme-switch__track {
  box-shadow: 0 1px 1px rgba(0, 0, 0, .25);
}

